@import url(https://fonts.googleapis.com/css?family=Rajdhani&display=swap);
*{
    margin: 0;
    padding: 0;
    font-family: 'Rajdhani', sans-serif;
}
body{
    background-color: #404040;
}
.App{
}

.Intro{
    -webkit-transition: 0.2s;
    transition: 0.2s;
    font-size: 2.4em;
    color:white;
    background: -webkit-gradient(linear, left top, left bottom, from(#000000), to(#404040));
    background: -webkit-linear-gradient(top, #000000, #404040);
    background: linear-gradient(to bottom, #000000, #404040);
}

.paddingToZero {
    -webkit-animation: paddingToZero 1.0s forwards; /* for less modern browsers */
            animation: paddingToZero 1.0s forwards;
}

@-webkit-keyframes paddingToZero {
    to {
        padding: 0;
    }
}

@keyframes paddingToZero {
    to {
        padding: 0;
    }
}

